import type { ButtonHTMLAttributes, ComponentType } from 'react';

import type { IconProps } from '@/icons';

type ButtonVariant = 'primary' | 'danger' | 'neutral' | 'outline';

const VARIANT_STYLES: Record<ButtonVariant, string> = {
  primary: 'bg-[#1692EC] text-white border-transparent hover:bg-[#147ED0]',
  danger: 'bg-[#F96464] text-white border-transparent hover:bg-[#e25656]',
  neutral: 'bg-gray-200 text-gray-900 border-transparent hover:bg-gray-300',
  outline: 'bg-white text-[#101828] border-[#D0D5DD] hover:bg-[#F7F9FB]',
};

type ButtonProps = {
  icon?: ComponentType<IconProps>;
  text: string;
  variant?: ButtonVariant;
  iconPosition?: 'left' | 'right';
} & ButtonHTMLAttributes<HTMLButtonElement>;

export function Button({
  icon: Icon,
  text,
  variant = 'primary',
  iconPosition = 'left',
  className,
  ...rest
}: ButtonProps) {
  const variantClass = VARIANT_STYLES[variant];
  const hasIcon = Boolean(Icon);
  const iconElement = hasIcon && Icon ? <Icon size={20} color="currentColor" className="shrink-0" /> : null;
  const contentClass = hasIcon ? 'justify-start' : 'justify-center';

  return (
    <button
      type="button"
      className={`inline-flex items-center ${contentClass} gap-3 rounded-[4px] border px-6 py-3 text-base font-semibold transition-colors focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-black ${variantClass} ${className ?? ''}`.trim()}
      {...rest}
    >
      {iconElement && iconPosition === 'left' ? iconElement : null}
      <span>{text}</span>
      {iconElement && iconPosition === 'right' ? iconElement : null}
    </button>
  );
}

